*{
  margin: 0;
  padding: 0;
}

body{
  background: url('https://t4.ftcdn.net/jpg/03/41/16/09/360_F_341160981_CFBrQpnDWWuMZqTpDPvOkHcdZeEikeXm.jpg');
  background: fixed;
  background: no-repeat;
  
  font-family: "Bree Serif", serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.rose{
  text-align: center;
  color: rgb(25, 245, 227);
}
.app{
   background: linear-gradient(90deg,#ff9000 0%, #00ffc8 80%);
   width: 95%;
   min-height: 200px;
   height: min-content;
   border-radius: 15px;
   padding: 20px;

  box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
  display: flex;
  justify-content: space-evenly;
}

.Question-Section
{
  position: relative;
  width: 100%;
  color: white;

}
.Question-Section h1 span{
  color: rgb(0, 255, 234);
  font-size: 40px;
  margin-bottom: 10px;
}
.home{
  font-size: 40px;
  margin-top: 10px;
  color: rgb(223, 27, 134);
}
.Answer-Section{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
}
button{
  width: 100%;
  font-size: 15px;
  color: #ffffff;
  background: linear-gradient(90deg,#ff9000 0%, #00ffc8 80%);
  border-radius: 15px;
  display: flex;
  padding: 10px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid blueviolet;
  cursor: pointer;
}
.bee{
  display: block;
  align-items: center;
  font-size: 30px;
  color: white;
  font-weight: bold;
}